video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
  
.hero-container {
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url('../../../public/images/img-1.jpeg') center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  padding:2rem;
  margin:0;
}

@media only screen and (max-width: 600px) {
  .hero-container {
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url('../../../public/images/img-1.jpeg') center center/cover no-repeat;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    padding:2rem;
    margin:0;
  }
}

.hero-container > h1 {
  color: #fff;
  font-size: 6rem;
  margin-top: -100px;
  text-shadow: 3px 3px 20px #000000;
  text-align: center;
  padding-top: 5rem;
  padding-bottom:1rem;
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  text-shadow: 3px 3px 20px #000000;
  text-align: center;
}

.hero-btns {
  margin-top: 32px;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}
  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 3.5rem;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 600px) {
    .hero-container > h1 {
      font-size: 2.2rem;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 1.5rem;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }