.feature-wrapper{
    padding: 2rem 0;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width : 100%;
    background-color:#242424;
    position: relative;
    overflow:hidden;
	color:white
}
.feature-wrapper>h1{
    padding-bottom:2rem;
}

.each-feature-wrapper{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width : 100%;
}


.Card{
	position:relative;
	text-align:center;
    display:flex;
	flex-wrap: wrap;
    justify-content: center;
    align-items: center;
	width:22vw;
	height: 22vw;
	border-radius:74% 82% 70% 88%;
	background-color:rgba(255, 205, 205, 0.9);
	cursor:pointer;
	z-index:1;
	transition:.5s;
	color:#000000;
	font-size: 1.5rem;
}

.Card:before,
.Card:after{
	content:'';
	position:absolute;
	top:0px;
	left:0px;
	width:100%;
	height:100%;
	z-index:-1;
	animation:RotateDiv 5s linear infinite;
}
.Card:before{
	border-radius:130% 151% 189% 166%;
	background-color:rgba(255,255,255,.7);
	animation-delay:0s;
	transition:.5s;
}
.Card:after{
	border-radius:145% 86% 80% 90%;
	background-color:rgba(212, 212, 212, 0.3);
	animation-delay:.2s;
	transition:.5s;
}
.Card:hover{
	background-color:rgba(255, 201, 201, 0.8);
	color:#fff;
}
.Card:hover:after{
	background-color: rgba(255, 131, 131, 0.7);
}
.Card:hover:before{
	background-color: rgba(255, 138, 138, 0.3);
}
.Card div{
	display:table-cell;
	vertical-align:middle;
}

@keyframes RotateDiv{
	0%{
		transform:rotate(0deg);
	}
	100%{
		transform:rotate(360deg);
	}
}


.right-pad{
    margin-right: 8vw;
}



.each-feature-detail-wrapper {
	font-size: 1.2rem;
	margin-top: 3rem;
	position: relative;
	background-color: rgb(255, 182, 182);
	width: 90%;
	height:32vw;
	text-align: center;
	color: black;
	padding: 2rem;
	/* Add default border-radius */
	border-radius: 15px;
	transition: border-radius 0.8s ease-out; /* Add transition property */	
	display:flex;
	justify-content: center;
	align-items: center;
	text-align: left;
}

.each-feature-detail-wrapper.card1 {
	/* Add specific border-radius for the first card */
	border-radius: 15px 50px;
}

.each-feature-detail-wrapper.card3 {
	/* Add specific border-radius for the third card */
	border-radius: 50px 15px;
}

.each-feature-detail ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
}


.Card:hover,
.Card.selected {
  background-color: rgba(255, 201, 201, 0.8);
  color: #fff;
}

.Card:hover:after,
.Card.selected:after {
  background-color: rgba(255, 131, 131, 0.7);
}

.Card:hover:before,
.Card.selected:before {
  background-color: rgba(255, 138, 138, 0.3);
}


@media only screen and (max-width: 700px) {
	.Card{
		position:relative;
		text-align:center;
		display:flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		width:28vw;
		height: 28vw;
		border-radius:74% 82% 70% 88%;
		background-color:rgba(255, 205, 205, 0.9);
		cursor:pointer;
		z-index:1;
		transition:.5s;
		color:#000000;
		font-size: 1.1rem;
	}
	.right-pad{
		margin-right: 5vw;
	}

	.each-feature-detail-wrapper {
		font-size: 0.9rem;
		height:100vw;
	}
	
  }